import React, { ReactNode } from "react";
import styled from "styled-components";

import { spacingMap } from "../styleguide";

type DrawerHeaderProps = {
	children?: ReactNode,
	className?: string,
};

const StyledDrawerHeader = styled.div`
	display: flex;
	flex-direction: column;
	padding: ${spacingMap.sm};
	gap: ${spacingMap.xs}
`;

const DrawerHeader = ({
	children,
	className,
}: DrawerHeaderProps) => (
	<StyledDrawerHeader className={className}>
		{children}
	</StyledDrawerHeader>
);

export default DrawerHeader;
